.contact {
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: rgba(0,0,0,0.1);
  padding: 16px;
}

.contact .row {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
}

@media (max-width: 767px) {
  .contact {
    width: unset;
    min-height: 100vh;
  }
}